import React from 'react';
import CM from './ClientManager';


class Chat extends React.Component<{ user: string }, { message: string; chatMsgs: any[] }>{
    constructor(props: any) {
        super(props);
        this.state = {
            message: "",
            chatMsgs: []
        }
    }
    componentDidMount() {
        CM.initChat(this.addMsg);
    }
    sendMsg = (event: any) => {
        event.preventDefault();
        const { user } = this.props;
        const { message } = this.state;
        CM.emitMsg(user, message);
        this.setState({
            message: ""
        })
    }

    addMsg = (msg: any) => {
        console.log(msg)
        var line = <div className='msgContainer'><div className='msgUsername'> {msg.user}: </div><div className='msgText'>{msg.msg}</div></div>
        let { chatMsgs } = this.state;
        chatMsgs.push(line);
        this.setState({
            chatMsgs
        })
        var elem = document.getElementById('txt-area');
        if(elem)
        elem.scrollTop = elem.scrollHeight;
    }

    handleChange = (event: any) => {
        const { value } = event.target;
        this.setState({
            message: value
        })
    }


    render = () => {
        const { message, chatMsgs } = this.state;
        return (
            <div className="chatContainer"  >
                <div className='txt-area' id='txt-area'>{chatMsgs}</div>
                <div className='chatInputs'>
                    <form onSubmit={this.sendMsg}>
                        <input
                            type='text'
                            id='msg'
                            value={message}
                            onChange={this.handleChange}
                            placeholder='enter your message here...'
                            autoComplete='off'
                            ></input>
                        <button disabled={message ? false : true}>Send</button>
                    </form>
                </div>
            </div>
        )
    }
}

export default Chat